import React, { memo } from 'react';
import { isEmpty as _isEmpty } from 'lodash';
import { compose, withState } from 'recompose';
import { IconButton, Menu, MenuItem } from '@material-ui/core';
import { MoreVert as MoreVertIcon } from '@material-ui/icons';
import PropTypes from 'prop-types';

import withRequest from '../../utils/with-request';
import TicketsService from '../../services/tickets';

const ITEM_HEIGHT = 48;

const Archive = memo(({
  anchorEl, setAnchorEl, archiveFetch, archiveLoading, doneTickets
}) => {
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  const handleArchive = () => {
    archiveFetch(doneTickets);
  };
  const disabledArchiveButton = (_isEmpty(doneTickets) || archiveLoading);
  return (
    <div>
      <IconButton
        aria-label="more"
        aria-controls="long-menu"
        aria-haspopup="true"
        onClick={handleClick}
      >
        <MoreVertIcon />
      </IconButton>
      <Menu
        id="long-menu"
        anchorEl={anchorEl}
        keepMounted
        open={open}
        onClose={handleClose}
        PaperProps={{
          style: {
            maxHeight: ITEM_HEIGHT * 4.5,
            width: '23ch'
          }
        }}
      >
        <MenuItem onClick={handleArchive} disabled={disabledArchiveButton}>
          Arquivar concludos
        </MenuItem>
      </Menu>
    </div>
  );
});
Archive.propTypes = {
  anchorEl: PropTypes.object,
  setAnchorEl: PropTypes.func,
  archiveFetch: PropTypes.func,
  archiveLoading: PropTypes.func,
  doneTickets: PropTypes.array
};

const withArchive = withRequest(TicketsService.archiveTicket, {
  name: 'archive',
  init: false,
  options: ({ setAlert, ticketsFetch, doneTickets }) => ({
    variables: { ticketsIds: doneTickets },
    onRequestFinished: () => {
      ticketsFetch(true);
      setAlert({
        alertMsg: 'Os tickets concludos foram arquivados',
        alertType: 'success',
        showAlert: true
      });
    },
    onRequestError: () => {
      setAlert({
        alertMsg: 'Erro ao arquivar tickets',
        alertType: 'warning',
        showAlert: true
      });
    }
  })
});

export default compose(
  memo,
  withArchive,
  withState('anchorEl', 'setAnchorEl', null),
  withState('isExportModalOpen', 'setIsExportModalOpen', false)
)(Archive);
